import { FormControl, ValidatorFn, Validators } from "@angular/forms";
import * as moment from "moment";
import { getFormControlName } from "src/app/shared/angular-utils";
import parsePhone from "../../../assets/js/utils/phoneparser"
export class CustomValidators {
    // static phoneNumber(countryPhoneCode = '*'): ValidatorFn {
    //   return Validators.pattern('([+]'+ countryPhoneCode + ')?([0-9]{9,12})$');
    // }
    static phoneNumber(formControl: FormControl) {
        if(!parsePhone(formControl.value).error){
            return null
        }else{
            return {'phoneNumber':true}
        }
    }
    static hasKeyValue (formControl: FormControl) {
            if(getFormControlName(formControl)){
                return null
            }else{
                return {'hasValue':true}
            }
        }
    static isDate(format?, validateKeyValue?) {
        validateKeyValue = validateKeyValue || false;
        format = format || false;
        return function (formControl: FormControl) {
            let isDate;
            if(validateKeyValue){
                isDate = momentIsDate(getFormControlName(formControl), format);
            }else{
                isDate = momentIsDate(formControl.value, format)
               
            }

            if(isDate){
                return null
            }else{
                return {'isDate':true}
            }

            function momentIsDate(date, format){
                return ((moment(date) as any)._d != 'Invalid Date') && (format ? ((moment(date) as any)._f == format) || (moment(date)).format(format) == date : true );
            }

        }
    }

    // static callbackValidator(func) {
    //     return function (formControl: FormControl) {
    //         return func(formControl)
    //     }
    // }
  }