  /*sticky*/

  initStickyScroll() {
    var previewContent = document.querySelector('#promotionDisplay');
    var elContainer = document.body,//document.querySelector('#wrapper'),
      elRoot = previewContent.parentElement as HTMLElement,
      elItem = previewContent as HTMLElement,
      elHiddenItem = document.createElement('div'),
      K = null,
      Z = 0,
      marginTop = 15,
      startPosition = null,
      marginBottom = 15;

    elContainer.onscroll = () => stickyScroll();

    elHiddenItem.style.zIndex = '-1';
    elHiddenItem.style.visibility = 'hidden';
    elHiddenItem.style.position = 'fixed';
    elHiddenItem.style.height = elContainer.scrollHeight - ((elRoot.offsetParent as HTMLElement).offsetTop + (elRoot.offsetParent as HTMLElement).offsetHeight) + marginBottom + 'px';
    elHiddenItem.style.bottom = Math.round(document.documentElement.getBoundingClientRect().bottom - elContainer.getBoundingClientRect().bottom) + 'px';

    elRoot.appendChild(elHiddenItem);


    function stickyScroll(reset?) {
      if (elRoot) {
        var elRootRect = elRoot.getBoundingClientRect(),
          R1bottom = elRoot.parentElement.getBoundingClientRect().bottom;

        if (elRootRect.bottom < R1bottom) {
          if (elItem == null) {
            var styles = getComputedStyle(elRoot, ''),
              string = '';
            for (var i = 0; i < styles.length; i++) {
              if (styles[i].indexOf('overflow') == 0 || styles[i].indexOf('padding') == 0 || styles[i].indexOf('border') == 0 || styles[i].indexOf('outline') == 0 || styles[i].indexOf('box-shadow') == 0 || styles[i].indexOf('background') == 0) {
                string += styles[i] + ': ' + styles.getPropertyValue(styles[i]) + '; '
              }
            }
            elItem = document.createElement('div');
            elItem.style.left = '';
            elItem.className = "stop";
            elItem.style.cssText = string + ' box-sizing: border-box; width: ' + elRoot.offsetWidth + 'px;';
            elRoot.insertBefore(elItem, elRoot.firstChild);
            var l = elRoot.childNodes.length;
            for (var i = 1; i < l; i++) {
              elItem.appendChild(elRoot.childNodes[1]);
            }
            elRoot.style.height = elItem.getBoundingClientRect().height + 'px';
            elRoot.style.padding = '0';
            elRoot.style.border = '0';
          }

          marginTop = marginTop || 0;
          marginBottom = marginBottom || 0;
          startPosition = startPosition || 0;

          var elItemRect = elItem.getBoundingClientRect(),
            Rh = elRootRect.top + elItemRect.height,
            contentHeight = elContainer.clientHeight,
            endPosition = Math.round(Rh - R1bottom),
            R2 = Math.round(Rh - contentHeight);


          if (elItemRect.height > contentHeight) {
            if (elRootRect.top < K) { // down scroll
              if (R2 + marginBottom > endPosition) { // don't get to the bottom

                if (Math.floor(elItemRect.bottom - contentHeight + marginBottom) <= Math.floor(elHiddenItem.getBoundingClientRect().bottom - contentHeight + marginBottom)) {

                  //elItem.style.width = elItemRect.width + 'px';
                  // elItem.style.left = elItemRect.left + 'px';
                  elItem.className = 'sticky';
                  elItem.style.top = contentHeight - elItemRect.height - marginBottom + 'px';
                  Z = (marginBottom + elRootRect.top + elItemRect.height - contentHeight);

                } else {
                  //elItem.style.width = '';
                  // elItem.style.left = '';
                  elItem.className = 'stop';
                  elItem.style.top = -Z + 'px';
                }
              } else {
                //elItem.style.width = '';
                // elItem.style.left = '';
                elItem.className = 'stop';
                elItem.style.top = -endPosition + 'px';
                Z = endPosition;
              }
            } else { // up scroll
              if (elRootRect.top - marginTop < 0) { //don't get to the top
                if (elItemRect.top - marginTop >= 0) {
                  // elItem.style.width = elItemRect.width + 'px';
                  // elItem.style.left = elItemRect.left + 'px';
                  elItem.className = 'sticky';
                  elItem.style.top = marginTop + 'px';
                  Z = elRootRect.top - marginTop;
                } else {

                  if (reset) {
                    elItem.className = 'sticky';
                    elItem.style.top = contentHeight - elItemRect.height - marginBottom + 'px';
                    Z = (marginBottom + elRootRect.top + elItemRect.height - contentHeight);
                  } else {


                    if (Math.round(elRootRect.bottom - elItemRect.bottom) < marginBottom) {
                      elItem.className = 'sticky';
                      elItem.style.top = contentHeight - elItemRect.height - marginBottom + 'px';
                      Z = (marginBottom + elRootRect.top + elItemRect.height - contentHeight);
                    } else {
                      //elItem.style.width = '';
                      // elItem.style.left = '';
                      elItem.className = 'stop';
                      elItem.style.top = -Z + (elHiddenItem.getBoundingClientRect().top - contentHeight) + 'px';

                    }

                  }

                }
              } else {
                //elItem.style.width = '';
                // elItem.style.left = '';
                elItem.className = '';
                elItem.style.top = '';
                Z = 0;
              }
            }
            K = elRootRect.top;
          } else {
            if ((elRootRect.top - marginTop) <= startPosition) {
              if ((elRootRect.top - marginTop) <= endPosition) {
                //elItem.style.width = '';
                // elItem.style.left = '';
                elItem.className = 'stop';
                elItem.style.top = -endPosition + 'px';
              } else {
                // elItem.style.width = elItemRect.width + 'px';
                // elItem.style.left = elItemRect.left + 'px';
                elItem.className = 'sticky';
                elItem.style.top = marginTop + 'px';
              }
            } else {
              //elItem.style.width = '';
              // elItem.style.left = '';
              elItem.className = '';
              elItem.style.top = '';
            }
          }
          //window.addEventListener('resize', function (e) {
          //stickyScroll(true);

          // if (elItem.classList.contains('sticky')) {
          //   elItem.style.width = '';
          //   elItem.style.left = '';
          //   elItem.className = 'stop';

          //   elItem.style.width = elItemRect.width + 'px';
          //   elItem.style.left = elItemRect.left + 'px';
          //   elItem.className = 'sticky';

          //   elItem.style.width = (elRootRect.width - 30) + 'px';
          // } else {
          //   elItem.style.width = '';
          //   elItem.style.left = '';
          // }

          //elRoot.children[0].style.width = getComputedStyle(elRoot, '').width
          //}, false);
        }
      }
    }
  }

