import { Directive, TemplateRef, ViewContainerRef, OnInit, AfterViewInit, ElementRef, OnChanges, Input, ChangeDetectorRef } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { debounceTime, tap } from 'rxjs/operators';

declare var $: any;

@Directive({
  selector: '[bootstrapSelect]',
  // host: {
  //   '[subscription]': 'onSubscription()',
  // }
  // inputs: [
	// 	"subscription"
	// ]
})
export class BootstrapSelectDirective implements OnInit, AfterViewInit, OnChanges{
  //@Input('bootstrap-select') bootstrapSelect;
  //@Input('subscription') subscription;
  selectpicker;
  private subscription:any;
  subscriptionChanges: BehaviorSubject<any>;
  @Input()
  set bootstrapSelect(value) {
    if(this.subscriptionChanges && this.subscription != JSON.stringify(value)){
      this.subscription = JSON.stringify(value);
      this.subscriptionChanges.next(this.subscription);
    }
  }


  constructor(
    private elementRef?: ElementRef,
    private templateRef?:TemplateRef<any>, 
    private viewContainerRef?:ViewContainerRef,
    private cdr?: ChangeDetectorRef) {
  }
  ngOnInit(): void {
    this.subscriptionChanges = new BehaviorSubject(this.subscription);
    this.subscriptionChanges.pipe(debounceTime(0)).subscribe((model) => {
      //$(this.selectpicker).selectpicker('render');
      $(this.selectpicker).selectpicker('refresh');
      if(this.selectpicker.options.length && this.selectpicker.options.selectedIndex == -1){
        $(this.selectpicker).val('val', [...this.selectpicker.options][0].value);
      }
    });
  }
  ngAfterViewInit(): void {
    this.viewContainerRef.createEmbeddedView(this.templateRef);
    let selectpicker = this.selectpicker = (this.viewContainerRef.get(0) as any).rootNodes[0];//this.elementRef.nativeElement
    this.initSelectpicker(selectpicker);
    this.cdr.detectChanges();

    $(this.selectpicker).selectpicker('render');
    // $(selectpicker).selectpicker('refresh');
    this.subscriptionChanges.next(this.subscription);
    
    
  }
  ngOnChanges(changes) {
    
    if(this.subscriptionChanges && Object.keys(changes).includes('bootstrapSelect') && this.subscription != JSON.stringify(changes.bootstrapSelect.currentValue)){
      
      this.subscription = changes.bootstrapSelect.currentValue;
      console.log("SELECT CHANGE", this.selectpicker, this.subscription);
      // $(this.selectpicker).selectpicker('render');
      // $(this.selectpicker).selectpicker('refresh');
 
      this.subscriptionChanges.next(this.subscription);
    }
    
  }

  public initSelectpicker(item){
    var $itemClone = $(item).clone(true).appendTo('body');
    $itemClone.css({
      display: 'block',
      width: 'max-content',
      overflow: 'initial'
    });

    $(item).selectpicker('destroy');

    $(item).selectpicker({
      style: `${item.disabled ? 'disabled' : ''} form-control`,
      width: item.classList.contains('auto-width') ? $itemClone[0].clientWidth + 'px' : item.classList.contains('inherit-width') ? 'inherit' : false,
      sanitize: false,
      showContent: false
    });

    if(item.options.length && item.options.selectedIndex == -1){
      $(item).val('val', [...item.options][0].value);
    }

    $(item).on("show.bs.select", function (event, libEvent) {
      $('.dropdown-menu > li > a').on('click', '.dropdown-content input', function (e) {
        e.preventDefault();
        e.stopPropagation();
      });
    });

    $(item).on('shown.bs.select', function (e, clickedIndex, isSelected, previousValue) {
      if (this.options.length) {
        clickedIndex.target.querySelector('.dropdown-menu').classList.remove('hidden');
      } else {
        clickedIndex.target.querySelector('.dropdown-menu').classList.add('hidden');
      }
      [...e.target?.parentElement?.querySelectorAll('.dropdown-menu .inner a')].forEach((item, i) => {
        let dataTitle = [...e.target.options].map(item => item.dataset.title || '');
        if (dataTitle[i]) item.title = dataTitle[i];
      });
    });

    if (item.classList.contains('noclose')) {
      $(item).selectpicker('refresh');
      //setTimeout(() => {
      item.parentElement.querySelector('button').click();
      document.querySelector('body').click();
      //}, 1);

    }

    $itemClone.remove();
  }

}
