export function objectFilter(obj, filtercheck) {
    let result = {};
    Object.keys(obj).forEach((key) => {
      if (filtercheck(obj[key])) result[key] = obj[key];
    })
    return result;
  };

export function unique(array, propertyName) {
  return array.filter((e, i) => array.findIndex(a => a[propertyName] === e[propertyName]) === i);
}

export function uniqueArr(arr){
  return [...new Set(arr)];
}

export function combineArrays(...arg){
  return [...[...arg].flat()];
}

export function redirectTo(path) {
  window.location.href = path;
}

export function randomString(i) {
  var rnd = '';
  while (rnd.length < i)
    rnd += Math.random().toString(36).substring(2);
  return rnd.substring(0, i);
};

export function camelize(str) {
  return str.replace(/(?:^\w|[A-Z]|\b\w)/g, function (word, index) {
    return index == 0 ? word.toLowerCase() : word.toUpperCase();
  }).replace(/\s+/g, '');
}

export function escapeString(str, slashLength = 2) {
  return str.replace(new RegExp('[-[\\]{}()*+?&.,\\\\^$|#\'\"]', 'gim'), (`${[...new Array(slashLength)].map(i=>'\\').join('')}$&`));
};

export function safetext(text){
	var table = {
		'<': 'lt',
		'>': 'gt',
		'"': 'quot',
		'\'': 'apos',
		'&': 'amp',
		'\r': '#10',
		'\n': '#13'
	};
	
	return text.toString().replace(/[<>"'\r\n&]/g, function(chr){
		return '&' + table[chr] + ';';
	});
};

export function templating(startSymbols = '[-', endSymbols = '-]', text, insideTags = true, calbackFn = (item)=>item){
  var query = new RegExp(`(?!\<(\\s*\\w*[^>]*))${escapeString(startSymbols, 1)}([\\u0400-\\u04FF. \\|\\-]|\\w)+(?=\\s|\\b|\\W|$)${escapeString(endSymbols, 1)}(?=\\s|\\S|.*<|)${!insideTags ? `((?=<(\s*\w*[^>]*)>|<\s*\/\s*\w*>)|(?!\w*[^<>]*>))` : ``}`, "gim");
  return text.replaceAll(query,calbackFn);
};

export function splitFirst(string, regexpOrSubstr:string|RegExp){
  return string.replace(regexpOrSubstr, '¬').split('¬');
}

export function getNestedValue(obj, key) {
  return key.split(".").reduce(function(result, key) {
     return result[key] 
  }, obj);
}

export function stripHTML(html) {
  var tmp = document.createElement("DIV");
  tmp.innerHTML = html;
  return tmp.textContent || tmp.innerText;
}

export function createElementFromHTML(html) {
  var div = document.createElement('div');
  div.innerHTML = html.trim();
  return div.firstChild; 
}

export function attrStr(attr, str){
  return `${attr}="${str}"`;
}

export function addDays(date, days) {
  var newDate = new Date(date.valueOf());
  newDate.setDate(newDate.getDate() + days);
  return newDate;
}

export function getDates(startDate, stopDate) {
  var dateArray = new Array();
  var currentDate = startDate;
  while (currentDate <= stopDate) {
    dateArray.push(new Date(currentDate));
    currentDate = addDays(currentDate,1);
  }
  return dateArray;
}

export function getUrlParameter(strParam, sParam, caseInsensitive?) {
  if (strParam) {
    var sPageURL = decodeURIComponent(strParam),
      sURLVariables = sPageURL.split(new RegExp('\\&|\\?', 'gim')),
      sParameterName,
      i,
      array = [];
    for (i = 0; i < sURLVariables.length; i++) {
      sParameterName = sURLVariables[i].split('=');
      if (sParam) {
        if (caseInsensitive ? sParameterName[0].toLowerCase() == sParam.toLowerCase() : sParameterName[0] === sParam) {
          return sParameterName[1] === undefined ? true : sParameterName[1];
        }
      } else {
        array.push(sParameterName);
        if (i + 1 == sURLVariables.length) {
          return array;
        }
      }
    }
  }
}

export function validURL(str) {
  var pattern = new RegExp('^((ft|htt)ps?:\\/\\/)'+ // protocol (? at the end)
    '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|'+ // domain name
    '((\\d{1,3}\\.){3}\\d{1,3}))'+ // OR ip (v4) address
    '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*'+ // port and path
    '(\\?[;&a-z\\d%_.~+=-]*)?'+ // query string
    '(\\#[-a-z\\d_]*)?$','i'); // fragment locator
  return !!pattern.test(str);
}

export function deepMerge(target, source) {
  for (var prop in source) {
      if (source.hasOwnProperty(prop)) {
          if (target[prop] && typeof source[prop] === "object") {
              deepMerge(target[prop], source[prop]);
          } else {
              target[prop] = source[prop];
          }
      }
  }

  return target;
}

export function deepCompare () {
  var i, l, leftChain, rightChain;

  function compare2Objects (x, y) {
    var p;

    // remember that NaN === NaN returns false
    // and isNaN(undefined) returns true
    if (isNaN(x) && isNaN(y) && typeof x === 'number' && typeof y === 'number') {
         return true;
    }

    // Compare primitives and functions.     
    // Check if both arguments link to the same object.
    // Especially useful on the step where we compare prototypes
    if (x === y) {
        return true;
    }

    // Works in case when functions are created in constructor.
    // Comparing dates is a common scenario. Another built-ins?
    // We can even handle functions passed across iframes
    if ((typeof x === 'function' && typeof y === 'function') ||
       (x instanceof Date && y instanceof Date) ||
       (x instanceof RegExp && y instanceof RegExp) ||
       (x instanceof String && y instanceof String) ||
       (x instanceof Number && y instanceof Number)) {
        return x.toString() === y.toString();
    }

    // At last checking prototypes as good as we can
    if (!(x instanceof Object && y instanceof Object)) {
        return false;
    }

    if (x.isPrototypeOf(y) || y.isPrototypeOf(x)) {
        return false;
    }

    if (x.constructor !== y.constructor) {
        return false;
    }

    if (x.prototype !== y.prototype) {
        return false;
    }

    // Check for infinitive linking loops
    if (leftChain.indexOf(x) > -1 || rightChain.indexOf(y) > -1) {
         return false;
    }

    // Quick checking of one object being a subset of another.
    // todo: cache the structure of arguments[0] for performance
    for (p in y) {
        if (y.hasOwnProperty(p) !== x.hasOwnProperty(p)) {
            return false;
        }
        else if (typeof y[p] !== typeof x[p]) {
            return false;
        }
    }

    for (p in x) {
        if (y.hasOwnProperty(p) !== x.hasOwnProperty(p)) {
            return false;
        }
        else if (typeof y[p] !== typeof x[p]) {
            return false;
        }

        switch (typeof (x[p])) {
            case 'object':
            case 'function':

                leftChain.push(x);
                rightChain.push(y);

                if (!compare2Objects (x[p], y[p])) {
                    return false;
                }

                leftChain.pop();
                rightChain.pop();
                break;

            default:
                if (x[p] !== y[p]) {
                    return false;
                }
                break;
        }
    }

    return true;
  }

  if (arguments.length < 1) {
    return true; //Die silently? Don't know how to handle such case, please help...
    // throw "Need two or more arguments to compare";
  }

  for (i = 1, l = arguments.length; i < l; i++) {

      leftChain = []; //Todo: this can be cached
      rightChain = [];

      if (!compare2Objects(arguments[0], arguments[i])) {
          return false;
      }
  }

  return true;
}