import { AbstractControl, FormArray, FormControl, FormGroup } from "@angular/forms";

/**
 * Deep clones the given AbstractControl, preserving values, validators, async validators, and disabled status.
 * @param control AbstractControl
 * @returns AbstractControl
 */
 export function cloneAbstractControl<T extends AbstractControl>(control: T): T {
  let newControl: T;

  if (control instanceof FormGroup) {
    const formGroup = new FormGroup({}, control.validator, control.asyncValidator);
    const controls = control.controls;

    Object.keys(controls).forEach(key => {
      formGroup.addControl(key, cloneAbstractControl(controls[key]));
    })

    newControl = formGroup as any;
  }
  else if (control instanceof FormArray) {
    const formArray = new FormArray([], control.validator, control.asyncValidator);

    control.controls.forEach(formControl => formArray.push(cloneAbstractControl(formControl)))

    newControl = formArray as any;
  }
  else if (control instanceof FormControl) {
    newControl = new FormControl(control.value, control.validator, control.asyncValidator) as any;
  }
  else {
    throw new Error('Error: unexpected control value');
  }

  if (control.disabled) newControl.disable({emitEvent: false});

  return newControl;
}

export function getRawValueDeep(data) {
  return (function inner(data) {
    // if (data instanceof FormArray) {
    //   return reduceChild(data);
    // } else if (data instanceof FormGroup) {
    //   return reduceChild(data);
    // }

    return reduceChild(data);

    function reduceChild(data) {
      data = data?.controls || data;
      if(Array.isArray(data)){
        return data.map((control) => {
          return control instanceof FormControl ? typeof control.value === 'object' && control.value !== null ? reduceChild(control.value) : control.value : inner((<any>control));
        });
      }else{
        return Object.keys(data).reduce((prev, cur) => {
          let control = data[cur];
          prev[cur] = control instanceof FormControl ? typeof control.value === 'object' && control.value !== null ? reduceChild(control.value) : control.value : inner((<any>control));
          return prev;
        }, {});
      }
    }
  })(data);
}

export function filterControls(controls, compFunc){
  if(Array.isArray(controls)){
    return controls.filter(control=>{
      return compFunc(control);
    });
  }else{
    return Object.keys(controls).filter(controlName=>{
      return compFunc(controls[controlName])
    }).reduce((prev,cur,i)=>{
      prev[cur] = controls[cur];
      return prev;
    },{});
  }
}

export function getFormControlName(control: AbstractControl): string | null {
  let group = <FormGroup>control.parent;

  if (!group) {
    return null;
  }

  let name: string;

  Object.keys(group.controls).forEach(key => {
    let childControl = group.get(key);

    if (childControl !== control) {
      return;
    }

    name = key;
  });

  return name;
}

export function compareByID(itemOne, itemTwo) {
  return itemOne && itemTwo && itemOne.id == itemTwo.id;
}

/*Custom reactive Forms functions*/
export function removeFromFormArray(formArray, value){
  let controlIndex;
  let controls = (formArray as FormArray).controls.filter((control, i)=>{
    if(control.value == value){
      controlIndex = i;
      return true;
    }
  });
  if(controls.length){
    formArray.removeAt(controlIndex);
  }
}

export function addToFormArray(formArray, value, changeIfContains?){
  if(changeIfContains){
    let controls = (formArray as FormArray).controls.filter(control=>control.value == value);
    if(controls.length){
      controls[0].setValue(value);
    }else{
      let formControl = new FormControl(value);
      (formArray as FormArray).push(formControl);
    }
  }else{
    let formControl = new FormControl(value);
    (formArray as FormArray).push(formControl);
  }
  
}

export function removeFormArrayControl(formArray, index) {
  (formArray as FormArray).removeAt(index);
}

export function removeFormGroupControl(formGroup, index) {
  let currentFormGroupName = getFormControlName(formGroup);
  let newFormGroup = this.formBuilder.group(Object.keys(formGroup.controls).reduce((prev, cur, i) => {
    if (i != index) {
      prev[cur] = formGroup.controls[cur];
    }
    return prev;
  }, {}));
  formGroup.parent.setControl(currentFormGroupName, newFormGroup);
}

export function addFormGroupControl(formGroup, defaultValue, defaultName = '', index?) {
  let currentFormGroupName = getFormControlName(formGroup);
  let newFormGroup = this.formBuilder.group(Object.keys(formGroup.controls).reduce((prev, cur, i, arr) => {
    if (index != undefined) {
      if (i == index) {
        prev[cur] = formGroup.controls[cur];
        prev[defaultName] = cloneAbstractControl(formGroup.controls[cur]);
      }
    } else {
      prev[cur] = formGroup.controls[cur];
      if (i == arr.length - 1) {
        prev[defaultName] = cloneAbstractControl(formGroup.controls[cur]);
      }
    }
    return prev;
  }, {}));
  formGroup.parent.setControl(currentFormGroupName, newFormGroup);
  formGroup = formGroup.parent.controls[currentFormGroupName];

  formGroup.controls[defaultName].reset(defaultValue);
  formGroup.controls[defaultName].markAsUntouched();
  //formGroup.controls[defaultName].updateValueAndValidity();
}

export function checkboxFormArrayHandler(el, formArray, value) {
  let index = formArray.controls.map(item => item.value).indexOf(value);
  let formControl = formArray.controls[index];

  if (!formControl) {
    formControl = new FormControl(value);
    formArray.push(formControl);
  }

  if (el.checked) {
    formControl.setValue(value);
  } else {
    formArray.removeAt(index);
  }
}

export function checkboxFormControlHandler(formControl, trueValue = true, falseValue = false) {
  if (formControl?.value == true) {
    formControl.setValue(trueValue);
  } else if (formControl?.value == false) {
    formControl.setValue(falseValue);
  }
}

export function manageFormControls(value, formControlsArray) {
  formControlsArray.forEach(element => {
    element.setValue(value);
  });
}

export function manageFormControl(value, formControl) {
  formControl.setValue(value);
  //this.cdr.detectChanges();
  return value;
}

export function renameFormControl(formControl, name, sort = true) {
  let currentControlName = getFormControlName(formControl);
  let formGroup = formControl.parent;

  Object.keys(formGroup.controls).forEach((cur, i) => {
    if (cur == currentControlName) {
      let newControl = cloneAbstractControl(formGroup.controls[cur]);
      formGroup.removeControl(cur);
      formGroup.addControl(name, newControl);

      newControl.updateValueAndValidity();
    }
  });

}

export function replaceFormControl(formControl, newFormControl){
  let currentControlName = getFormControlName(formControl);
  let formGroup = formControl.parent;
  Object.keys(formGroup.controls).forEach((cur, i) => {
    if (cur == currentControlName) {
      let newControl = cloneAbstractControl(newFormControl);
      formGroup.removeControl(cur);
      formGroup.addControl(currentControlName, newControl);

      //newControl.updateValueAndValidity();
    }
  });
}

/*end Custom reactive Forms functions*/