import { AfterViewInit, Directive, ElementRef, OnInit } from '@angular/core';

declare var $: any;

@Directive({
  selector: '[summernote]'
})
export class SummernoteDirective implements OnInit, AfterViewInit{

  constructor(
    //private template:TemplateRef<any>, 
    //private view:ViewContainerRef,
    private elementRef: ElementRef
    ) { }

    ngOnInit(): void {
    
    }
    ngAfterViewInit(): void {
      //this.view.createEmbeddedView(this.template);
      this.initSummernote(this.elementRef.nativeElement);
    }

    public initSummernote(textarea){

      // function validateTextArea(textarea) {
      //   if (textarea.required) {
      //     if (textarea.value) {
      //       textarea.closest('.form-group').classList.remove('has-error')
      //     } else {
      //       if (!isHidden(textarea)) {
      //         textarea.closest('.form-group').classList.add('has-error');
      //       }
      //     }
      //   }
      // }

      function textAreaHandler(textarea, value) {
        var div = document.createElement('DIV');
        div.innerHTML = value;
        if (!div.textContent.trim()) {
          textarea.value = '';
          textarea.innerText = '';
        } else {
          textarea.value = value;
          textarea.innerText = value;
        }

        textarea.dispatchEvent(new Event('change'));
        textarea.dispatchEvent(new Event('input'));
        textarea.dispatchEvent(new Event('blur'));
      }

      let navigation = [
        ['style', ['bold', 'italic', 'underline']],
        ['color', ['forecolor']],
        ['para', ['paragraph']],
        ['para', ['ul', 'ol']],
        ['table', ['table']],
        ['insert', ['link']]
      ];

      $(textarea).summernote('destroy');

      $(textarea).summernote({
        height: +textarea.getAttribute('height') || 50,
        callbacks: {
          onKeyup: function (e) {
            textarea.dispatchEvent(new Event('input'));
            if (e.target.closest('.form-group.has-error') || textarea.classList.contains('is-invalid')) {
              setErrorFocusStyles(e.target.closest('.note-editor'));
            } else {
              //$(e.target.closest('.note-editor')).removeAttr('style');
              //setBaseFocusStyles(e.target.closest('.note-editor'));
            }
          },
          onChange: function (e) {
            textAreaHandler(this, e);
            //validateTextArea(this);
          },
          onBlur: function (e) {
            textAreaHandler(this, e.target.innerHTML);
            //validateTextArea(this);

          },
          onFocus: function (e, e2) {
            setBaseFocusStyles(e.target.closest('.note-editor'));
          },
          onInit: function (e) {
            //changeButtonSize();

            e.editor.attr('tabindex', '0');
            e.editingArea.attr('tabindex', '0');
            e.editingArea.find(".note-editable").attr('tabindex', '0');

            e.editingArea.css('cursor', 'auto');
            e.editingArea.on('click', function (e) {
              e.stopPropagation();
              return false;
            });

            e.editor.on('focus', focusHandler);
            e.editor.on('focusout', focusoutHandler);
            e.editingArea.on('focus', focusHandler);
            e.editingArea.on('focusout', focusoutHandler);
            e.editingArea.find(".note-editable").on('focus', focusHandler);
            e.editingArea.find(".note-editable").on('focusout', focusoutHandler);

            // function changeButtonSize(){
            //   let toolbar = e.toolbar ? e.toolbar[0] : undefined;
            //   if(toolbar){
            //     let buttons = toolbar.querySelectorAll('.note-btn');
            //     buttons.forEach(button=>{
            //       button.classList.remove('btn-sm');
            //       button.classList.add('btn-xs');
            //     });
            //   }
            // }

            function focusHandler() {
              if (e.editor[0].closest('.form-group.has-error') || textarea.classList.contains('is-invalid')) {
                setErrorFocusStyles(e.editor);
              } else {
                //e.editor.removeAttr('style');
                setBaseFocusStyles(e.editor);
              }
            }

            function focusoutHandler() {
              e.editor.removeAttr('style');
            }

            this.value ?
              e.editingArea.find(".note-editable").html(this.value) :
              e.editingArea.remove(".note-editable");
            this.placeholder ?
              e.editingArea.find(".note-placeholder").html(this.placeholder) :
              e.editingArea.remove(".note-placeholder");
            textAreaHandler(this, e.editingArea.find(".note-editable").html());
          }
        },
        toolbar: textarea.classList.contains('toolbar') ? textarea.closest('#tab-questions') || textarea.closest('#tab-categories') ? navigation.filter(item => !['para', 'table'].includes((item as any)[0])) : navigation : '',
        placeholder: true
      });
    
      function setBaseFocusStyles(el) {
      $(el).css({
        'border-color': 'rgb(102, 175, 233)',
        'box-shadow': 'inset 0px 0px 0px 0px rgba(0, 0, 0, 0.075), 0px 0px 6px 0px rgba(102, 175, 233, 0.537)',
        'transition': 'border-color ease-in-out .15s, box-shadow ease-in-out .15s'
      });

      const div = $(el).find(".note-editable").get(0);

      function setCaretPosition() {
          const range = document.createRange();
          range.selectNodeContents(div);
          range.collapse(false);
          const sel = window.getSelection();
          sel.removeAllRanges();
          sel.addRange(range);
        }
        setCaretPosition();
      }

      function setErrorFocusStyles(el) {
        $(el).css({
          'border-color': '#a94442',
          'box-shadow': 'inset 0 0px 0px rgba(169,68,66,0.75), 0 0 6px rgba(169,68,66,0.75)',
          'transition': 'border-color ease-in-out .15s, box-shadow ease-in-out .15s'
        });
      }
    }

    
}
